	module.exports = {"data":{"talkJson":{"description":"Why do people succeed? Is it because they're smart? Or are they just lucky? Neither. Analyst Richard St. John condenses years of interviews into an unmissable 3-minute slideshow on the real secrets of success.","meta":{"duration":"PT3M34S"},"presenter":{"name":"Richard St. John"},"slug":"ted-talk-richard-st-johns-8-secrets-of-success","source":{"id":"UN79-Tv5GHY","provider":"youtube"},"tag":["TEDTalks","Success","Career"],"title":"8 Secrets Of Success"},"allTalkJson":{"edges":[{"node":{"description":"Facebook COO Sheryl Sandberg looks at why a smaller percentage of women than men reach the top of their professions -- and offers 3 powerful pieces of advice to women aiming for the C-suite.","meta":{"duration":"PT15M29S"},"presenter":{"name":"Sheryl Sandberg"},"source":{"id":"18uDutylDa4","provider":"youtube"},"slug":"why-we-have-too-few-women-leaders","tag":["TEDTalks","Career","Feminism"],"thumbnails":{"default":{"url":"https://i.ytimg.com/vi/18uDutylDa4/default.jpg","height":90,"width":120}},"title":"Why we have too few women leaders"}},{"node":{"description":"Stephen Wolfram, creator of Mathematica, talks about his quest to make all knowledge computational -- able to be searched, processed and manipulated. His new search engine, Wolfram Alpha, has no lesser goal than to model and explain the physics underlying the universe.","meta":{"duration":"PT20M30S"},"presenter":{"name":"Stephen Wolfram"},"source":{"id":"60P7717-XOQ","provider":"youtube"},"slug":"computing-a-theory-of-everything","tag":["TEDTalks","Physics"],"thumbnails":{"default":{"url":"https://i.ytimg.com/vi/60P7717-XOQ/default.jpg","height":90,"width":120}},"title":"Computing a theory of everything"}},{"node":{"description":"It is the first TEDx talk of the founder of Free Software movement. Stallman, RMS for short, has changed the world with his vision of freedom for the digital age. He launched the GNU operating system, used with Linux as a component, and inspired the development of Creative Commons licences and Wikipedia project. In this talk, Stallman describes how nonfree programs give companies control of their users and what users can do in order to recover control over their computing.","meta":{"duration":"PT13M40S"},"presenter":{"name":"Richard Stallman"},"source":{"id":"Ag1AKIl_2GM","provider":"youtube"},"slug":"free-software-free-society-richard-stallman","tag":["GNU General Public License (Software License)","GNU/Linux (Operating System)","Free Software Foundation","TEDTalks","Geneva"],"thumbnails":{"default":{"url":"https://i.ytimg.com/vi/Ag1AKIl_2GM/default.jpg","height":90,"width":120}},"title":"Free software, free society"}},{"node":{"description":" Historian George Dyson tells stories from the birth of the modern computer from its 16th-century origins to the hilarious notebooks of some early computer engineers.","meta":{"duration":"PT17M20S"},"presenter":{"name":"George Dyson"},"source":{"id":"EF692dBzWAs","provider":"youtube"},"slug":"the-birth-of-the-computer","tag":["TEDTalks"],"thumbnails":{"default":{"url":"https://i.ytimg.com/vi/EF692dBzWAs/default.jpg","height":90,"width":120}},"title":"The Birth of the Computer"}},{"node":{"description":"Most 12-year-olds love playing videogames â€” but Thomas Suarez taught himself how to create them. After developing iPhone apps like \"Bustin Jeiber,\" a whack-a-mole game, he is now using his skills to help other kids become developers.","meta":{"duration":"PT4M41S"},"presenter":{"name":"Thomas Suarez"},"source":{"id":"Fkd9TWUtFm0","provider":"youtube"},"slug":"a-12-year-old-app-developer","tag":["TEDTalks","Education"],"thumbnails":{"default":{"url":"https://i.ytimg.com/vi/Fkd9TWUtFm0/default.jpg","height":90,"width":120}},"title":"Thomas Suarez: A 12-year-old app developer"}},{"node":{"description":"Treo creator Jeff Hawkins urges us to take a new look at the brain -- to see it not as a fast processor, but as a memory system that stores and plays back experiences to help us predict, intelligently, what will happen next.","meta":{"duration":"PT22M10S"},"presenter":{"name":"Jeff Hawkins"},"source":{"id":"G6CVj5IQkzk","provider":"youtube"},"slug":"how-brain-science-will-change-computing","tag":["TEDTalks","Brain Science","Neuroscience","Artificial Intelligence (AI)","Inspiring"],"thumbnails":{"default":{"url":"https://i.ytimg.com/vi/G6CVj5IQkzk/default.jpg","height":90,"width":120}},"title":"How brain science will change computing"}},{"node":{"description":"The controversial website WikiLeaks collects and posts highly classified documents and video. Founder Julian Assange, who's reportedly being sought for questioning by US authorities, talks to TED's Chris Anderson about how the site operates, what it has accomplished -- and what drives him. The interview includes graphic footage of a recent US airstrike in Baghdad.","meta":{"duration":"PT19M34S"},"presenter":{"name":"Julian Assange"},"source":{"id":"HNOnvp5t7Do","provider":"youtube"},"slug":"why-the-world-needs-wikileaks","tag":["TEDTalks","WikiLeaks"],"thumbnails":{"default":{"url":"https://i.ytimg.com/vi/HNOnvp5t7Do/default.jpg","height":90,"width":120}},"title":"Why the world needs WikiLeaks"}},{"node":{"description":"This talk was given at a local TEDx event, produced independently of the TED Conferences. Simon Peyton Jones wants children not just to consume technology, but to be creative with it. What does that mean for teaching computer science in schools, and why is the rest of the world watching England?\n\nSimon Peyton Jones is a principal researcher at Microsoft Research in Cambridge. His research focus is in programming language design and implementation, and the purely-functional language Haskell in particular. He is a founder member and current chair of the Computing at School working group, which has played a central role in the recent reform of the school Computing curriculum.\n\nAt TEDxExeter 2014 our speakers and performers connected us with other worlds. Our talks exposed corruption in big business, shared effective approaches to tackling social inequality and gave a voice to those whose human rights are under threat. We explored the impact of fast changing technologies on all our lives. We journeyed through fire and forest to frozen landscapes. We were challenged to consider worlds of extremes, cutting edge controversies and risky opportunities.\nhttps://twitter.com/simonpj0\n\nVideo Production Chromatrope (http://chromatrope.co.uk/)\nProduction Manager Andy Robertson (http://www.youtube.com/familygamertv)\n\nAbout TEDx, x = independently organized event In the spirit of ideas worth spreading, TEDx is a program of local, self-organized events that bring people together to share a TED-like experience. At a TEDx event, TEDTalks video and live speakers combine to spark deep discussion and connection in a small group. These local, self-organized events are branded TEDx, where x = independently organized TED event. The TED Conference provides general guidance for the TEDx program, but individual TEDx events are self-organized.* (*Subject to certain rules and regulations)","meta":{"duration":"PT14M50S"},"presenter":{"name":"Simon Peyton Jones"},"source":{"id":"Ia55clAtdMs","provider":"youtube"},"slug":"teaching-creative-computer-science","tag":["TEDTalks","Politics"],"thumbnails":{"default":{"url":"https://i.ytimg.com/vi/Ia55clAtdMs/default.jpg","height":90,"width":120}},"title":"Teaching creative computer science"}},{"node":{"description":"It's not a demo, more of a philosophical argument: Why did Sergey Brin and his team at Google want to build an eye-mounted camera/computer, codenamed Glass? Onstage at TED2013, Brin calls for a new way of seeing our relationship with our mobile computers -- not hunched over a screen but meeting the world heads-up.","meta":{"duration":"PT7M16S"},"presenter":{"name":"Sergey Brin"},"source":{"id":"IessjPY9gwI","provider":"youtube"},"slug":"why-google-glass","tag":["TEDTalks","Design","User Interface","Google"],"thumbnails":{"default":{"url":"https://i.ytimg.com/vi/IessjPY9gwI/default.jpg","height":90,"width":120}},"title":"Why Google Glass?"}},{"node":{"description":"Anyone ever give you advice on how to remain a programmer? To avoid being \"promoted\" into positions away from technology and code? Anyone ever tell you at school or university that you needed social skills to be a good developer? Did you know, without having had half a dozen different jobs, that all development roles are not created equal? Is it true that moving jobs a lot is a Bad Thing?\n\nIn this session I'm going to share some lessons I learnt the hard way while managing my career as a Java developer. I'm going to tell you secrets that others don't want to share. And I'll give you tools for working out what your next steps are.\n\nIf nothing else, you'll get to laugh at the (many) mistakes I've made in my search for The Perfect Job.","meta":{"duration":"PT54M19S"},"presenter":{"name":"Trisha Gee"},"source":{"id":"LlAn452X4Lc","provider":"youtube"},"slug":"career-advice-for-programmers","tag":["Career","The perfect Job","GOTO Conference"],"thumbnails":{"default":{"url":"https://i.ytimg.com/vi/LlAn452X4Lc/default.jpg","height":90,"width":120}},"title":"Career Advice for Programmers"}}]}},"pathContext":{"slug":"ted-talk-richard-st-johns-8-secrets-of-success","tags":["TEDTalks","Success","Career"]}}

